.contactButton {
  border: 2px solid;
  outline: none;
  background-color: #f37b83;
  padding: 20px 0;
  font-size: 14px;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  width: 100%;
  letter-spacing: 8px;
  transition: opacity 0.3s ease;
  cursor: pointer;
}
