.mainBlock {
  background-color: #f9f0ff;
  display: flex;
  min-height: 100vh;
}
.mainText {
  width: 510px !important;
  background-color: #f9f0ff;
  padding: 10% 10% 10% 10%;
}
.mainPhoto {
  width: calc(100% - 510px) !important;
  background-size: cover;
  background-position: center;
}
.name {
  font-size: 80px;
  font-weight: 800;
  line-height: 94px;
  color: #f37b83;
  padding: 20px 0;
}
.hello {
  color: #492d31;
  font-size: 28px;
  line-height: 28px;
  margin-bottom: 70px;
}
.mainText p {
  margin-top: 70px;
}

@media (max-width: 767px) {
  .mainPhoto {
    display: none;
  }
  .mainText {
    margin-top: 100px;
  }
}
